import React,{ Component }  from 'react';
import '../../App.css';
import { Card ,Button,Input} from 'antd';
import { CopyOutlined} from '@ant-design/icons';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import Node from '../../ABI/node';
const { TextArea } = Input;

class App extends Component {
      state = {
        message:'',
        ver:'',
        downloadAdr:'',
        UpdateInfo:'',
        info:'',
      };

  async componentDidMount(){

    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
        //初始化Provider
        const provider = window['ethereum']
        if(provider.chainId === '0x1'){this.setState({chainId:"以太链"});
          this.setState({disabled:"disabled"})};
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()
        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
          console.log("accountsChanged:" + accounts)
        })
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
          console.log(networkVersion)
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}

  }

  PushNewVer = async () =>{
    await Node.methods.PushNewVer(this.state.ver).send({from:window.defaultAccount});
  }

  downloadAdr = async () =>{
    await Node.methods.downloadAdr(this.state.downloadAdr).send({from:window.defaultAccount});
  }
  PushUpdateInfo = async () =>{
    await Node.methods.PushUpdateInfo(this.state.UpdateInfo).send({from:window.defaultAccount});
  }
  Pushinfo = async () =>{
    await Node.methods.Pushinfo(this.state.info).send({from:window.defaultAccount});
  }

  render(){

  return (
    <div>
        <Card type="inner" size='small' title="管理员界面" >
            <Input  addonBefore='最新版本'  placeholder="如:23"
              onChange={event=>{this.setState({ver:event.target.value})}}
              suffix={
                  <Button type='primary' size="small"
                    onClick={this.PushNewVer}
                    >发送</Button>
              }
            />
            <Input  addonBefore='前端链接'  placeholder="前端下载链接"
              style={{marginTop:'8px'}}
              onChange={event=>{this.setState({downloadAdr:event.target.value})}}
              suffix={
                  <Button type='primary' size="small"
                    onClick={this.downloadAdr}
                    >发送</Button>
              }
            />
            <div style={{textAlign:'right',marginTop:'8px'}}>
              <TextArea rows={4}  placeholder="版本更新提示信息"
                    onChange={event=>{this.setState({UpdateInfo:event.target.value})}}
              />
              <Button type='primary' size="small" 
                onClick={this.PushUpdateInfo}
                >发送</Button>
            </div>
            <div style={{textAlign:'right',marginTop:'8px'}}>
              <TextArea rows={4}  placeholder="全局信息"
                    onChange={event=>{this.setState({info:event.target.value})}}
              />
              <Button type='primary' size="small" 
                onClick={this.Pushinfo}
                >发送</Button>
            </div>

            <div style={{color:"green",fontSize:"13px",textAlign:'center',marginTop:'10px'}}>
                  {this.state.message}
                <CopyToClipboard text={this.state.message}>
                    <Button size='small'><CopyOutlined /></Button>
                </CopyToClipboard>
            </div>
         </Card>
    </div>
  );
}
}

export default App;
