import React,{ Component }  from 'react';
import '../../App.css';
import { Card } from 'antd';
import { Link } from 'react-router-dom';


class mainCardShentai extends Component {
    state = {
        LGDEX:''
    };

    async componentDidMount(){
        //设置LGDEX链接
        const url = await window.location.host;
        this.setState({LGDEX:'https://' + url + '/LGDEX/index.html'});
        console.log(this.state.LGDEX);
    }
 
  render(){
    
  return (
              <Card type="inner" title="" size='small'>
                <div>
                    <div >
                        <Link to="/STAirdrop">
                            <div style={{width:'25%',float:'left'}}>
                                <div style={{textAlign:'center'}}>
                                    <div className='kongtou' ></div>
                                </div>
                                <div style={{textAlign:'center',color:'black'}}>空投机</div>
                            </div>
                        </Link>
                        <Link to="/hongbao">
                            <div style={{width:'25%',float:'left'}}>
                                <div style={{textAlign:'center'}}>
                                    <div className='hongbao' ></div>
                                </div>
                                <div style={{textAlign:'center',color:'black'}}>抢红包</div>
                            </div>
                        </Link>
                        <a href={this.state.LGDEX}>
                            <div style={{width:'25%',float:'left'}}>
                                    <div style={{textAlign:'center'}}>
                                        <div className='LGDEX' ></div>
                                    </div>
                                    <div style={{textAlign:'center',color:'black'}}>LG交易所<br/></div>
                                </div>
                        </a>

                        {/* <Link to="/messageHome"> */}
                            <div style={{width:'25%',float:'left'}}>
                                  <div style={{textAlign:'center'}}>
                                      <div className='message' ></div>
                                  </div>
                                  <div style={{textAlign:'center',color:'black'}}>链短信<br/></div>
                            </div>
                        {/* </Link> */}
                        <div style={{clear:'both'}}></div>
                    </div>

                    <div style={{marginTop:'15px'}}>
                        <Link to="/TkFactoryHome">
                            <div style={{width:'25%',float:'left'}}>
                                <div style={{textAlign:'center'}}>
                                    <div className='tokenFactory' ></div>
                                </div>
                                <div style={{textAlign:'center',color:'black'}}>发行通证</div>
                            </div>
                        </Link>
                        <Link to="/LockIndex">
                            <div style={{width:'25%',float:'left'}}>
                                <div style={{textAlign:'center'}}>
                                    <div className='lock' ></div>
                                </div>
                                <div style={{textAlign:'center',color:'black'}}>锁仓机</div>
                            </div>
                        </Link>
                        <div style={{clear:'both'}}></div>
                    </div>
                </div>
              </Card>
  );
}
}

export default mainCardShentai;
