import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Link } from 'react-router-dom';
import { Table, Card ,Button,Input,Popover, Spin,Progress,Modal} from 'antd';
import { QuestionCircleOutlined} from '@ant-design/icons';
import Hongbao from '../../../ABI/hongbao';
import erc20abi from '../../../ABI/ERC20.abi.json';
import {CopyToClipboard} from 'react-copy-to-clipboard';

class HBhome extends Component {
      state = {
        requests:[],
        HBid:'',
        HBpassword:'0',
        onoff:'none',
        onoff2:'none',
        lastHB:'?',
        lastHBsymbol:'',
        message:"",
        ye:'',
        visible:false,
        balance:'',
        amount:'',
        lownBalance:'',
        userName:'',
        remark:'',
        HBsymbol:'',
        tokenAdr:'',
        loadwindow:false, //加载窗
        loading:'block', //加载中
        complete:'none', //加载完成
        loadmessage:'',  //加载窗信息
      };

  async componentDidMount(){


    if(typeof window.ethereum !== 'undefined'){
      const ethereum = window.ethereum
      //禁止页面自动刷新，metamask要求写的
      ethereum.autoRefreshOnNetwoorkChange = false
      try{
        //第一次链接Metamask
        const accounts = await ethereum.enable()
    
        //初始化Provider
        const provider = window['ethereum']
        
        if(provider.chainId === '0x1'){this.setState({chainId:"以太链"});
          this.setState({disabled:"disabled"})};
  
        //获取用户帐号
        window.defaultAccount = accounts[0].toLowerCase()

        //切换Metamask帐号
        ethereum.on('accountsChanged', function (accounts){
        })
  
        //切换Metamaskm网络ID
        ethereum.on(function (networkVersion){
        })
  
      }catch(e){}
    }else{console.log('没有metamask')}


  const HBtotal = await Hongbao.methods.HBid().call();

    const dataSource2 = []
    for(var i=0; i<=HBtotal; i++ ){
      const HBinfo2 = await Hongbao.methods.getHBinfo(i).call({from:window.defaultAccount});
      if(HBinfo2[8]===HBinfo2[9]){HBinfo2[10]= '已'}
      if(HBinfo2[5] !== '0'){
      dataSource2.push(HBinfo2);
      this.setState({message:"加载中……"+ i + '=>' + HBtotal});
      }
    }
      for(var ii=0; ii<dataSource2.length; ii++ ){
        const ERC20 = await new web3.eth.Contract(erc20abi.abi,dataSource2[ii][3]);
        const decimals = await ERC20.methods.decimals().call();
        dataSource2[ii][6] =  parseFloat(dataSource2[ii][6]*10**(-decimals) ).toFixed(1);
        this.setState({message:"加载中……"+ i + '=>' + HBtotal});
      }
      this.setState({requests:dataSource2});
      this.setState({message:""});
  }


  getHB =async () => {
    await Hongbao.methods.getHB(this.state.HBid, this.state.HBpassword)
      .send({from:window.defaultAccount})
      .on('transactionHash',(transactionHash)=>{
        if(transactionHash.length !== 0){
          this.setState({
            loadwindow: true,
            loadmessage:"打包中……",
            loading:'block',
            complete:'none'
          });
        }
      })
      .on('receipt',(receipt)=>{
        if(receipt.status === true){
          this.setState({loadmessage:"领取成功",
            loading:'none',
            complete:'block',  
          })
        }
      });
      this.open();
      this.setState({loadwindow: false});
  }

  open = async () => {
    const lastHBadr = await Hongbao.methods.lastTokenAdr(window.defaultAccount).call();
    const ERC20 = new web3.eth.Contract(erc20abi.abi,lastHBadr);
    const lastHBsymbol = await ERC20.methods.symbol().call();
    const decimals = await ERC20.methods.decimals().call();
    this.setState({lastHBsymbol:lastHBsymbol});
    const lastHB = await Hongbao.methods.lastHB(window.defaultAccount).call();
    if(lastHBsymbol === 'HBTC'){
    const _lastHB = ((parseFloat(web3.utils.fromWei(lastHB,'Gwei')))/10).toFixed(2);
    this.setState({lastHB:_lastHB});
    this.setState({lastHBsymbol:'聪BTC'});
    }
    if(lastHBsymbol === 'ETH'){
      const _lastHB = parseFloat(lastHB*10**(-decimals)).toFixed(8);
      this.setState({lastHB:_lastHB});
    }
    if(lastHBsymbol !== 'ETH' && lastHBsymbol !== 'HBTC'){
      const _lastHB = parseFloat(lastHB*10**(-decimals)).toFixed(2);
      this.setState({lastHB:_lastHB});
    }
  }
  
  close = () => {
    this.setState({onoff:"none"});
  }

  open2 = () => {
    this.setState({onoff2:"block"});
  }

  close2 = () => {
    this.setState({onoff2:"none"});
  }

  handleChange =(value) =>{
    console.log(`${value}`);
     this.setState({HBid:`${value}`});
  }

  handleCancel = () => {
    this.setState({visible: false})
  };

  //加载窗开关
  loadwindowClose = () => {
    this.setState({loadwindow: false})
  };
 
  render(){

    const columns = [
      {
        title: '',
        dataIndex: '0',
        key:'0',
        width:30,
      },
      {
        title: 'Who',
        dataIndex: '1',
        ellipsis: 'true',
        width: 60,
      },
      {
        title: '留言',
        dataIndex: '2',
        ellipsis: 'true',

      },
      {
        title: '剩',
        dataIndex: '5',
        ellipsis: 'true',
        width: 35,
      },
      {
        title: '领',
        dataIndex: '10',
        width: 40,
      }
  
    ];

    const LastHB = []
      LastHB.push(this.state.lastHB, this.state.lastHBsymbol)

      const content = (
        <div>
          <p>1. 手续费及挖矿比率可能会适时调整</p>
          <p>2. 发红包者可以在首页贡献值内领取LG矿，不用每发一次红包就领一次，可以一个月领一次</p>
          <p>3. 手续费收益的10~50%将用于回购LG或添加LG流动池</p>
        </div>
      );

      const content2 = (
        <div>
          ID:{this.state.HBid}&emsp;&emsp;{this.state.HBsymbol}&ensp;
          <CopyToClipboard text={this.state.tokenAdr}>
          <Button size='small' type='primary' >复制合约地址</Button>
          </CopyToClipboard>
        </div>
      );


  return (
    <div>
    <Card  type="inner" size='small' title="红包(LG&ge;100, LGC&ge;10)"  >
      <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
            {this.state.message}
      </div>
      <Table size='small' 
          columns={columns}  
          dataSource={this.state.requests} 
          
          onRow = {(record) => {
            return {
              onClick: async () => {
                  this.setState({visible: true})
                  this.setState({tokenAdr: record[3]})
                  this.setState({HBid: record[0]})
                  this.setState({balance: record[6]})
                  this.setState({amount: record[5]})
                  this.setState({lownBalance: parseFloat(web3.utils.fromWei(record[7],'ether')).toFixed(2) })
                  this.setState({userName: record[1]})
                  this.setState({remark: record[2]})
                  const ERC20 = await new web3.eth.Contract(erc20abi.abi,record[3]);
                  const symbol = await ERC20.methods.symbol().call();
                  this.setState({HBsymbol: symbol})
                }
              }
              }
              }

          />
      <Button  shape='round' type="primary" block color='green' size="middle" style={{marginTop:'6px'}} >
          <Link to="/HBgive">发红包</Link>
      </Button>
    </Card>

    
    <Card type="inner" size='small'  >
    <Button shape='round' type="dashed" block  size="small"  onClick={() => {this.open2()}}>说明</Button>
      <div style={{display:this.state.onoff2}}>
          <div>1. 持用100或以上LG，是发、领红包的前提条件</div>
          <div>2. 一个帐号只可领取一次</div>
          <div>3. 红包总额5%挖矿LG由发红包者获得，5%为手续费用于回馈所有LG成员
                <Popover content={content} title="说明" trigger="click">
                    <QuestionCircleOutlined />
                </Popover>
          </div>
          <Button shape='round' block type="dashed" size="small" onClick={() => {this.close2()}}>关闭</Button>
      </div>
    </Card>
    
    <Modal title={content2} visible={this.state.visible} 
        onCancel={this.handleCancel} 
        footer={[
        ]}
    > 
        <div style={{
          textAlign:'center', 
          width: '256px',
          margin: '0 auto'
        }}>
          <div className='hongbaoHome'>
            <div style={{textAlign:'center',
              fontSize:'30px',
              color:'#ff006f',
              position:'relative',
              top:'55px',
              }}
            >
              <b onClick={this.open}>{LastHB}</b>
            </div>
          </div>
          <div style={{
            background:'rgb(254,73,82)',
            borderBottomLeftRadius: 10,
            borderBottomRightRadius: 10,
          }}>
            <div style={{
                color:'yellow',
                textAlign:'left',
                marginLeft:'10px', 
                marginRight:'5px', 
            }}>
              <div>
                <span style={{color:'black'}}>总余额：</span>
                {this.state.balance}&nbsp;&nbsp;&nbsp;&nbsp;
                <span style={{color:'black'}}>红包数：</span>
                {this.state.amount}&nbsp;
              </div>
              <div>
                <span style={{color:'black'}}>平均手气：</span>
                {parseFloat(this.state.balance / this.state.amount).toFixed(2)}
              </div>
              <div>
                <span style={{color:'black'}}>条  件：</span>
                帐户内 &ge;{this.state.lownBalance}{this.state.HBsymbol}
              </div> 
              <div>
                <span style={{color:'black'}}>发送者：</span>
                {this.state.userName}
              </div>
              <div>
                <span style={{color:'black'}}>留  言：</span>
                {this.state.remark}
              </div>
              <div>
                <span style={{color:'black'}}>密码：</span>
                <Input style={{ width: '70%',backgroundColor:'rgb(254,73,82)',color:'white'}} 
                  placeholder="输入密码，无密空着"  
                  size='small'
                  onChange={event=>{this.setState({HBpassword:event.target.value})}}
                />
              </div>
              <div style={{
                textAlign:'center'
                }}>
                <Button shape='circle' type="primary"  size='large' 
                  style={{marginBottom:'5px',marginTop:'5px'}}
                  danger
                  onClick={() => {this.getHB()}}>開
                </Button>
              </div>
            </div>
          </div>
        </div>
      </Modal>
      {/* 加载窗  */}
      <Modal  visible={this.state.loadwindow} 
        onCancel={this.loadwindowClose} 
        footer={[
          <div style={{textAlign:'center'}} >
            <Button type='primary' onClick={this.loadwindowClose}>关闭</Button>
          </div>,
           ]}
      >
        <div style={{textAlign:'center'}}>
              <div style={{display:this.state.loading}}>
                <Spin size="large" />
                <div style={{color:"#1890ff",fontSize:"13px",textAlign:'center'}}>
                  {this.state.loadmessage}
                </div>
              </div>
              <div style={{display:this.state.complete}}>
                <Progress type="circle" percent={100} width={45}/>
                <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                  {this.state.loadmessage}
                </div>
              </div>
        </div>
      </Modal>
    </div>
  );
}
}

export default HBhome;
